.Joke {
    display: flex;
    border-bottom: 2px solid #eeeeee;
    align-items: center;
    justify-content: space-around;
    font-weight: 400;
    padding: 1rem;
}

.Joke-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20%;
    margin-right: 1rem;
}

.Joke-text {
    width: 75%;
    font-size: 1.2rem;
}

.Joke-votes {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    font-weight: 300;
    border: 4px solid red;
    box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
    user-select: none;
}

.fa-arrow-up,
.fa-arrow-down {
    font-size: 1.5rem;
    cursor: pointer;
    margin: 10px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.Joke-smiley {
    font-size: 2.4rem;
    margin-left: 3px;
    border-radius: 50%;
    box-shadow: 0 7px 38px rgba(0, 0, 0, 0.2), 0 7px 12px rgba(0, 0, 0, 0.1);
}

@media (max-width: 400px) {
    .Joke-votes {
        width: 2.4rem;
        height: 2.4rem;
        line-height: 2.4rem;
        font-size: 1.4rem;
        align-self: center;
        grid-area: B;
        border: 3px solid red;
    }

    .fa-arrow-up,
    .fa-arrow-down {
        font-size: 1.5rem;
        margin: 5px;
    }

    .fa-arrow-up {
grid-area: A;
    }

    .fa-arrow-down {
grid-area: C;
    }

    .Joke-buttons {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas:
                "A A A A   B B B B"
                "C C C C   B B B B";
    }

    .Joke-smiley {
        font-size: 2.4rem;
        border-radius: 50%;
    }


}