.JokeList {
    display: flex;
    width: 80%;
    height: 80%;
}
.JokeList-sidebar {
    background: #9303A7;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 30%;
    text-align: center;
    box-shadow: 0 10px 38px rgba(0,0,0, 0.5), 0 10px 12px rgba(0,0,0,0.3);
    z-index: 2;
}
.JokeList-title {
    font-size: 4rem;
    margin: 2rem;
    color: white;
    font-weight: 400;
    user-select: none;
}
.JokeList-title span {
    font-weight: 700;
    letter-spacing: 0;
    user-select: none;
}
.JokeList-sidebar img {
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 10px 38px rgba(0,0,0, 0.5), 0 10px 12px rgba(0,0,0,0.3);
}
.JokeList-jokes {
    height: 90%;
    background: #fff;
    align-self: center;
    width: 70%;
    overflow: scroll;
    box-shadow: 0 10px 38px rgba(0,0,0, 0.5), 0 10px 12px rgba(0,0,0,0.3);
}
.JokeList-spinner {
    color: #fff;
    text-align: center;
}
.JokeList-getmore {
    font-size: 1.5rem;
    width: 65%;
    border-radius: 1rem;
    padding: 1rem 2rem;
    color: #fff;
    font-weight: 700;
    border: none;
    outline: none;
    margin: 2rem;
    word-spacing: 1rem;
    cursor: pointer;
    transition: 0.8s cubic-bezier(0.2,1,0.2,1);
    background: rgb(252,113,123);
    background: linear-gradient(136deg, rgba(179,229,252,1) 50%, rgba(240,98,146,1) 50%);
    box-shadow: 0 10px 38px rgba(0,0,0, 0.5), 0 10px 12px rgba(0,0,0,0.3);
}
.JokeList-getmore:hover {
    transform: translateY(-3px);
    box-shadow: 0 15px 30px rgba(0,0,0, 0.25);

}

@media (max-width: 400px) {
    .JokeList {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90%;

    }
    .JokeList-sidebar{
        width: 100%;
        height: 12%;
        flex-direction: row;
        justify-content: space-around;
    }
    .JokeList-title {
        font-size: 3rem;
        margin: 0 0 0 1rem;
        color: white;
        font-weight: 400;
    }
    .JokeList-title span {
        font-weight: 700;
        letter-spacing: 0;
    }
    .JokeList-sidebar img {
        width: 12%;
    }
    .JokeList-jokes {
        height: 90%;
        background: #fff;
        align-self: center;
        width: 90%;
        overflow: scroll;
        box-shadow: 0 10px 38px rgba(0,0,0, 0.5), 0 10px 12px rgba(0,0,0,0.3);
    }
    .JokeList-spinner {
        color: #fff;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .JokeList-getmore {
        font-size: 1.2rem;
        width: 30%;
        padding: 0.5rem 1rem;
        margin: 1rem;

    }
}