.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: rgb(252,113,123);
  background: linear-gradient(136deg, rgba(179,229,252,1) 50%, rgba(240,98,146,1) 50%);
  font-family: 'Rubik', sans-serif;
  color: #78909c;
}
@media (max-width: 400px) {
  .App{
    align-items: unset;
    width: 100%;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
  }

}